@mixin set-background($color-map) {
  @for $i from 1 through 5 {
    &:nth-child(#{$i}) {
      background: map-get($color-map, #{$i});
    }
  }
}

%table-body {
  display: block;
  box-shadow: $box-shadow;
}

%borderd-edges {
  &:first-child,
  &:last-child {
    overflow: hidden;
  }

  &:first-child {
    @include border-radius(0 $border-radius-xl 0 0);
  }

  &:last-child {
    @include border-radius(0 0 $border-radius-xl $border-radius-xl);
  }
}

.serpbook {
  padding-top: 0;
  visibility: visible;
  opacity: 1;
  @include transition(all .3s linear);

  &.hidden {
    padding-top: 100px;
    visibility: hidden;
    opacity: 0;
  }

  &-table {
    position: relative;
    text-align: center;

    tr {

      &:last-child {
        td {
          border-color: none;
          border-width: 0;
        }
      }
    }

    th {
      position: relative;
      color: $white;
      vertical-align: middle;
      @include set-background($thead-colors);
    }

    td {
      font-weight: $font-weight-light;
      color: $white;
      vertical-align: middle;
      border-bottom: 1px solid rgba($black, .1);
      @include set-background($tbody-colors);
    }
  }

  &-nav {
    top: 0;

    font: $thead-font-base;

    th {
      padding: ($table-cell-padding * 1.25) $table-cell-padding;
      font-weight: $font-weight-normal;

      &:first-child,
      &:last-child {
        overflow: hidden;
      }

      &:first-child {
        @include border-radius($border-radius-xl 0 0 0);
      }

      &:last-child {
        @include border-radius(0 $border-radius-xl 0 0);
      }
    }

    &.fixed {
      position: fixed;
      top: 50px;
      z-index: $zindex-sticky;
      width: 100%;
      max-width: 1080px;
      @include transition(all .3s linear);

      th {
        @include border-radius(0);
      }
    }
  }

  &-values {
    @extend %table-body;
    @include border-radius(0 0 $border-radius-xl $border-radius-xl);
    font: $tbody-font-base;

    tr {
      @extend %borderd-edges;

      &:first-child {
        @include border-radius(0);
      }
    }

    td {
      padding: ($table-cell-padding) $table-cell-padding;

      &:not(:first-child):not(:nth-child(2)) {
        font-size: $h4-font-size * 1.2;
      }
    }
  }

  &-extra {
    @extend %table-body;
    @include border-radius($border-radius-xl);
    position: relative;
    margin: 0 0 ($spacer * 4);
    font: $tbody-font-small;

    tr {
      @extend %borderd-edges;
    }

    tr:nth-child(2) {
      overflow: hidden;
      @include border-radius( 0 $border-radius-xl 0 0);
    }

    .table-label {
      position: absolute;
      top: -21px;
      padding: 0 $grid-gutter-width;
      font-size: $font-size-base;
      font-weight: $font-weight-bold;
      color: $white;
      background: $dark;
      @include border-radius($border-radius-xl $border-radius-xl 0 0);
    }

    td {
      padding: ($table-cell-padding * .625) $table-cell-padding;

      &:not(:first-child):not(:nth-child(2)) {
        font-size: $h4-font-size;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}
